import { axios } from '@/utils/request'
const api = {
    siteMarketPre: '/api/siteMarket'
}
export function getAllBillsAPI(token){
    return axios({
        url: `${api.siteMarketPre}/getAllBills`,
        method: 'GET',
        params:{token}
    })
}
export function bizRegionAPI(params){//
    return axios({
        url: `${api.siteMarketPre}/bizRegion`,
        method: 'POST',
        params
    })
}
export function addCreditAPI(data){//
    return axios({
        url: `${api.siteMarketPre}/addCredit`,
        method: 'POST',
        data
    })
}
export function situationAPI(token){//
    return axios({
        url: `${api.siteMarketPre}/situation`,
        method: 'GET',
        params:{token}
    })
}
export function getAllCouponsAPI(token){
    return axios({
        url: `${api.siteMarketPre}/getAllCoupons`,
        method: 'GET',
        params:{token}
    })
}